import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { del, get, patch, post, put, query } from '@placeos/ts-client';
import { Observable, of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class PlaceService {
  private apiUrl = `/api/staff/v1/guests`;
  private peopleUrl = `/api/staff/v1/people`;
  private apiKey = `aed95f28b5a445eb2b30243a8524d85f.gN36Ag5rR7d7FMsfta5YNLi0TBCpxeyBopqTQvSueXo`;

  private httpOptions = {
    headers: new HttpHeaders({
      'X-API-Key': this.apiKey
    })
  }

  constructor(private http: HttpClient) { }

  getData(params?: any): Observable<any> {
    return get(`${this.apiUrl}?email=${params}`);
  }
  
  getPeople(params?: any): Observable<any> {
    let httpParams = new HttpParams();
    if (params){
      for (const key in params){
        if (params.hasOwnProperty(key)) {
          httpParams = httpParams.set(key, params[key]);
        }
      }
    }
    return this.http.get(this.peopleUrl, { params:httpParams, ...this.httpOptions });
  }
}
